<template>
  <div>
    <el-card shadow='hover' class='card'>
      <div class='list' v-for='item in list' :key='item.workid' :v-if='listEmpty'>
        <div class='item'>
          <admin-report :workid='item.workid' :type='item.type' :userid='item.userid' :target='item.target'
            :targetid='item.targetid' :content='item.content' :time='item.time' :original='item.original'
            v-if='item.type==0'></admin-report>
          <admin-auth :workid='item.workid' :type='item.type' :userid='item.userid' :realname='item.realname'
            :authtype='item.authtype' :authname='item.content' :time='item.time' v-if='item.type==1'></admin-auth>
        </div>
      </div>

      <div class='empty' v-if='listEmpty'>
        <el-image :src="require('@/../public/assets/empty.png')" style='width:100px'></el-image>
        <div>{{$t('Admin.ListEmpty')}}</div>
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios';
import * as server from '../../../server.js'
import Cookies from 'js-cookie'
import { customAlphabet } from 'nanoid';
const nanoid = customAlphabet('1234567890', 9);

export default {
  name: 'admin-index',
  components: {
    AdminReport: () => import('./AdminReport.vue'),
    AdminAuth: () => import('./AdminAuth.vue')
  },
  data () {
    return {
      list: [],
      listEmpty: true
    }
  },
  methods: {
    getList () {
      const loadingInstance = this.loading();
      axios({
        method: 'post',
        url: server.default.getAdminWork,
        data: new URLSearchParams({
          'cookie': Cookies.get('loginCookie'),
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'ADMINWORK_GETLIST_COMPLETE') {
            this.list = response.data.list;
            this.listEmpty = false;
          }
          else if (response.data == 'ADMINWORK_GETLIST_EMPTY') {
            this.list = [];
            this.listEmpty = true;
          }
        }
      })
    },
    completeWork (workid, choice) {
      const loadingInstance = this.loading();
      axios({
        method: 'post',
        url: server.default.completeAdminWork,
        data: new URLSearchParams({
          'alertid': nanoid(),
          'cookie': Cookies.get('loginCookie'),
          'workid': workid,
          'choice': choice
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data == 'ADMINWORK_COMPLETEWORK_COMPLETE') {
            for (let i in this.list) {
              if (this.list[i].workid == workid) {
                this.list.splice(i, 1);
              }
            }
            if (this.list.length == 0) {
              this.listEmpty = true;
            }
          }
          else if (response.data == 'ADMINWORK_ALERTID_TAKEN') {
            this.completeWork(workid, choice);
            return;
          }
        }
      })
    },
  },
  mounted () {
    this.$bus.$on('completeAdminWork', this.completeWork);

    this.getList();
  },
  beforeDestroy () {
    this.$bus.$off('completeAdminWork');
  }
}
</script>

<style scoped>
.card {
  width: 90%;
  margin-left: 5%;
  border-radius: 20px;
  min-height: 800px;
}
.empty {
  margin-top: 280px;
}
.item {
  margin-top: 30px;
}
</style>